<template>
  <div class="container">
    <div id="divlyDiv" style="height: 600px; width: 800px;"></div>
    <div class="box">AAAAAAAAAAAAAa</div>
    <xml id="toolbox" style="display: none">
      <block type="controls_if"></block>
      <block type="controls_repeat_ext"></block>
      <block type="logic_compare"></block>
      <block type="math_number"></block>
      <block type="math_arithmetic"></block>
      <block type="text"></block>
      <block type="text_print"></block>
    </xml>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        demoWorkspace:null
      }
    },
    methods: {
    },
    computed: {},
    props: [],
    created(){
    },
    mounted () {
      try {
        console.log(1)
        this.demoWorkspace =Blockly.inject('divlyDiv',
          {
            media: '/static/media/',
            toolbox: document.getElementById('toolbox')
          });
      }catch (err){
        console.log(err)
      }
    },
    destroyed(){

    },
    beforeRouteLeave (to, from, next) {
      next();
    }
  }
</script>
<style scoped lang="scss">
</style>




